.text{
    font-family: "Roboto Mono",sans-serif;
    font-size: 15px;
    line-height: 28px;
    font-weight: 300;
    color: #2a2a2a;
}

.title {
    color: #000000;
    margin: 30px;
    line-height: 1.285714285714286em;
    text-transform: uppercase;
    position: relative;
}

.title h3 {
    font-size: 35px;
    font-weight: 400;
    margin: 0;
}

.title::before {
    content: '';
    position: absolute;
    height: 1px;
    width: 100%;
    border-top: 1px solid #002fef;
    bottom: -30px;
}