.projectsBlock {
    min-height: 100vh;
    background-color:  #e8e7e7;
}

.projectsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;

}

.header {
    margin-bottom: 5vh;
}


.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px;
    align-items: flex-start;
}

@media screen and (max-width: 999px) {
    .projects {
        justify-content: center;
    }
}
