.projectBlock {
    width: 400px;
    background-color: #e8e7e7;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

}

.icon {
    min-height: 285px;
    width: 285px;
    background-color: dodgerblue;
}

.viewBtn{

}

.projectInfo{
    padding: 25px 20px;

}

.projectTitle{
    color: #000;
    font-size: 16px;
    line-height: 1.75em;
    font-weight: 500;
    letter-spacing: .15em;
    text-transform: uppercase;
    margin: 0;
}

.description {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    float: left;
    padding: 0 14px 0 0;
    margin: 0 9px 0 0;
    color: #777;
    font-size: 13px;
    letter-spacing: .05em;
    z-index: 8;
}
